:root {
  --primary-light: #8abdff;
  --primary-own:#0D3D56;
  --primary-dark: #5b0eeb;
  
  --white: #FFF;
  --greyLight-1: #E4EBF5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;
  --greyDark-1: #BABECC;
  --background-1:#EBECF0;
}

@media screen and (min-width:851px) {
  html{
      font-size: 62.5%;
  }

  body{
    background-color: var(--background-1) !important;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    padding: 2rem 3rem;
  }
  .main-container{
    overflow: hidden;
  }
.heading-container{
  color: var(--primary-own);
  font-size: 2.2rem;
  text-align: center;
  text-shadow: 0 0.1rem 0.1rem rgba(0,0, 0, 0.5);
  font-weight: bolder;
  word-spacing: 0.6rem;
  letter-spacing: 0.1rem;
}
.covid{
  width: 2rem;
}
.ani-card{
  padding: 2rem 2rem;
  border-radius: 1rem;
  box-shadow: .3rem .3rem .6rem var(--greyDark-1), 
  -.2rem -.2rem .5rem var(--white) !important;
  background: linear-gradient(140deg, rgba(235,236,240, 0.22), rgba(248, 247, 247, 0.25));
}
.sub-heading{
  font-size: 1.8rem;
  font-weight: 750;
  color: var(--primary-own);
  /* text-shadow: 0 0.1rem 0.1rem rgba(0,0, 0, 0.5); */
}
.text{
  font-size: 1.7rem;
  color: var(--primary-own);
  /* text-shadow: 0 0.1rem 0.1rem rgba(0,0, 0, 0.5); */
}
.time-container{
  display: flex;
  justify-content: space-around;
}
.time{
  font-size: 1.7rem;
  font-weight: 500;
  color: var(--primary-own)
}


.input-text{
  /* width: 20.4rem; */
  /* height: 4rem; */
  border: none;
  border-radius: 0rem 1rem 1rem 0rem;
  font-size: 1.4rem;
  padding-left: 2rem;
  background: none;
  font-family: inherit;
  color: var(--primary-own);
  box-shadow:inset .2rem .2rem .5rem var(--greyDark-1), 
              inset -.2rem -.2rem .5rem var(--white) !important; 
  outline: none;
  width: 100%;
  height: 4.5rem;    
  font-size: 2rem;           
} 
.input-text:focus{
  box-shadow:.3rem .3rem .6rem var(--greyDark-1), 
  -.2rem -.2rem .5rem var(--white) !important;  
}
::placeholder { 
  color: var(--primary-own) !important;
}
.email-container{
  display: flex;
  justify-content: center;
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 3rem;
}
.email-icon{
display: flex;
justify-content: center;
align-items: center;
border:0.1rem solid;
padding: 1rem;
border-radius: 1rem 0rem 0rem 1rem;
color: var(--primary-own);
border-color: #dddddd;
}
.india-world-container{
  display: flex;
  justify-content: space-around;
  padding-bottom: 2rem;
}
.india{
width: 45%;
}
.world{
  width: 45%;
}
.data-countary-container{
  text-align: center;
  font-size: 2rem;
}
.covid-data-main-div{
  padding: 0rem 3rem;
}
.country{
  color: #311d3f;
  font-weight: 900;
}
.country-data{
  color: #800000;
  letter-spacing: 0.1rem;
  font-weight: 600;
}
.child-data{
  color: var(--primary-own);
  font-size: 1.9rem;
  font-weight: 600;
}
.death{
  font-size: 1.9rem;
  font-weight: bold;
  color: #cc0000;
}
.recovered{
  font-size: 1.9rem;
  font-weight: 700;
  color: #00802b;
}
.active{
  color: #662200;
  font-size: 1.9rem;
  font-weight: 800;
}
.critical{
  color: #ff884d;
  font-size: 1.9rem;
  font-weight: 900;
}
.all-data-container{
  display: grid;
  grid-gap: 3rem;
  grid-template-columns: 32% 32% 31%;
  padding: 2rem;
}
.search-container{
  display: grid;
  grid-gap: 4rem;
  grid-template-columns: auto auto auto;
}
.ring{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 15rem;
  background:transparent;
  border-radius: 50%;
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 0 0.1rem 0.1rem rgba(0,0, 0, 0.5);

  color: #ac550e;
  letter-spacing: 0.4rem;
  text-transform: uppercase;
  text-shadow: 0 0 0.1rem #ac550e;
  box-shadow: .3rem .3rem .6rem var(--greyDark-1), 
             -.2rem -.2rem .5rem var(--white) !important;
  position: relative;           
}
.ring:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 96%;
  height: 96%;
  border: 0.3rem solid transparent;
  border-top: 0.3rem solid #fff000;
  border-right: 0.3rem solid #fff000;
  border-radius: 50%;
  animation: animateCircle 2s linear infinite;
}
.ring>span{
  display: block;
  position: absolute;
  right: 0;
  background: #fff000;
  background: transparent;
  width: 50%;
  height: 0.5rem;
  transform-origin: left;
  animation: animate 2s linear infinite;
}
.ring>span:before{
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 50%;
  background: #fff000;
  right: -0.7rem;
  top: -0.6rem;
  box-shadow: 0 0 2rem #fff000;
}

@keyframes animateCircle {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg)
  }
}
@keyframes animate {
  0%{
    transform: rotate(50deg);
  }
  100%{
    transform: rotate(405deg)
  }
}
.loading-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  position: fixed;
}

}



